import React from "react";
import {useState,useEffect} from 'react';
import { Link } from 'react-router-dom';
import {postActivity,getActivity} from '../actions/index';
import { useDispatch,useSelector } from 'react-redux';
import './Actividades.css';
import StyleHome from './home.modul.css';

function validar (input){

    if(!input.name){
        console.log(input.name)
        alert('ingresar name');
    }else if (!input.level){
        alert('ingrese nivel de dificultad');
    }else if (!input.time){
        alert('ingrese tiempos de actividad');
    }else if(!input.season){
        alert('ingrese estacion del año');
    }else if(input.countriId){
        alert('ingrese pais con actividad creada');
    };
   
};

export default function CrearActividad (){

       const dispatch= useDispatch();
       const contriSelects=useSelector((state => state.allCountries));
       const [,setError]= useState({});
       const [input ,setInput]=useState({
           name:"",
           level:"",
           time:"",
           season:"",
           countryId:[]
       });

       useEffect(()=>{
           dispatch(getActivity());
       },[dispatch]);

       function hadleDelete(event){
           setInput({
               ...input,
               countryId: input.countryId.filter(elem => elem !== event)
           });
       };

       function hadleChange(event){
             setInput ({
                 ...input,
                 [event.target.name]: event.target.value
             });
       };

       function hadleSelect (event){
               setInput ({
                   ...input,
                   countryId:[...input.countryId,event.target.value]
               });
       };

       function hadleSeason (event){
           setInput ({
              ...input,
              season:event.target.value 

           });
       };


       function hadleLevel (event){
             setInput({
                 ...input,
                 level: event.target.value
             });
       };

       function hadleSubmit (event){
           event.preventDefault()
           setError(validar({
               ...input,
               [event.target.value]:event.target.value
           }));
           dispatch(postActivity(input))
             setInput({
                name:"",
                level:"",
                time:"",
                season:"",
                countryId:[]
             });
       };


    return (
          <div className="CreaActividad">
              <h1 className="Title">Crea Actividad</h1>
              <div>
                  <Link to='/home'><button className={StyleHome.btnAdmin}>Volver</button></Link>
              </div>
              <div className={StyleHome.body}>
                  <form className="probe">
                        <div className="divActividades">
                            <label className="Text">Actividad:</label>
                            <input className={StyleHome.btnAdmin} type='text' value={input.name} name='name' 
                            onChange={(event) => hadleChange(event)}></input>
                        </div>
                        <div className="divActividades">
                             <label className="Text">Level:</label>
                             <select className={StyleHome.btnAdmin} onChange={(event) => hadleLevel(event)}>
                                 <option value='1'>1</option>
                                 <option value='2'>2</option>
                                 <option value='3'>3</option>
                                 <option value='4'>4</option>
                                 <option value='5'>5</option>
                             </select>
                        </div>
                        <div className="divActividades">
                            <label className="Text">Time:</label>
                            <input className={StyleHome.btnAdmin} type='string' value={input.time} name='time'
                            onChange={(event) => hadleChange(event)}></input>
                        </div>

                        <div className="devActividades">
                            <label className="text">Season</label>
                            <select className={StyleHome.btnAdmin} onChange={(event) => hadleSeason(event)}>
                                <option value='verano'>Summer</option>
                                <option value='Otoño'>Fall</option>
                                <option value='Invierno'>Winter</option>
                                <option value='primavera'>Spring</option>
                            </select>
                        </div>

                        <div  className="div Actividades">
                            <label className="text">Countries: <select className={StyleHome.btnAdmin} onChange={(event)=> hadleSelect(event)}>
                              {
                                 contriSelects.map((event) => (                       //agregar evento a un pais
                                     <option value={event.id}>{event.name}</option>
                                 )) 
                              }
                            </select>
                            </label>
                           <button className={StyleHome.btnAdmin} type='submit' onClick={(event)=> hadleSubmit(event)}> Add </button>  

                        </div>
                  </form>

                  <div className={StyleHome.divPais}>
                     {
                         input.countryId.map(elem =>   //borrar  paises 
                            <div key={elem.name} className={StyleHome.Pais}>
                               <h6>{elem}</h6>
                               <button className="boton" onClick={() => hadleDelete(elem)}>X</button>
                            </div>
                            )
                     }
                  </div>

              </div>
          </div>  

    );

}