import React from 'react';
import {Link} from 'react-router-dom';
import {useDispatch,useSelector} from 'react-redux';
import {getDetail} from '../actions';
import { useEffect } from 'react';


export default function Detail (props){

    const dispatch = useDispatch();

    useEffect(()=>{

        dispatch(getDetail(props.match.params.id));
    },[dispatch,props.match.params.id]);

    const Country=useSelector((state)=> state.detail);
    
    return(
        <>
           <div className='DivVolver'>
                <Link to='/home'><button className='ButtonVolver'>Volver</button></Link>   
           </div>
           <div className='ContenedorDetails'>

            { Country.Tourisms ?
               
               <div className='detalle'>
                   <h1>{Country.name}</h1>
                   <img src={Country.image_flag} alt='' width='250px' height='175px' margin='3px'/>
                   <h2>ID:{Country.id}</h2>
                   <h2>Continent:{Country.continent}</h2>
                   <h2 className='capital' alt='no existe capital'>Capital:{Country.capital || 'no existe capital'}</h2>
                   <h2 alt='subregion no existe'>Subregion:{Country.subregion || 'subregion null'}</h2>
                   <h3>Area Km2:{Country.area}</h3>
                   <h3>Poblacion:{Country.population}</h3>
               
            {
              Country?.Tourisms === undefined || Country?.Tourisms?.length === 0 ? 
                 <div className='ContenedorActividad'>
                     <h1 className='NoActivity'>No hay actividades</h1>
                     <div>
                       <Link to='/activity'>
                         <button className='BotonActividades'>Crear Actividad</button>
                       </Link>
                     </div>
                  </div> :

                  Country?.Tourisms.map((activity)=>(
                      <div className='actividad'>
                         <h5 className='datos'>Actividad: {activity.name}</h5>
                         <h5 className='datos'>Level: {activity.level}</h5>
                         <h5 className='datos'>Season: {activity.season}</h5>
                         <h5 className='datos'>Time: {activity.time}</h5>                   
                      </div>     
                  ))
               }
            
            </div> : <p>No se encontro  detalle :c</p>
            }
          </div>
       </>
    )
};