
const initialState={
    countries:[],
    allCountries:[],
    activity:[],
    detail:[]
}

function rootReducer (state=initialState,action){
    switch (action.type){
        case 'GET_COUNTRIES':
               return{
                    ...state,
                    countries:action.payload,
                    allCountries:action.payload
               };
        case 'GET_SEARCH':
                return{
                     ...state,
                     countries:action.payload
                };
       case 'GET_ACTIVITY':
                    return{
                         ...state,
                         activity:action.payload
                    };
        case 'FILTER_BY_CONTINENT':
                const allCountri=state.allCountries
                const stateFilter= action.payload === 'All' ? allCountri : allCountri.filter(el=> el.continent === action.payload)
                return{
                    ...state,
                    countries:stateFilter
                    };
        case 'FILTER_BY_AREA':
               const Area= action.payload === 'desc' ? state.countries.sort((a,b)=> a.area - b.area ) :
               state.countries.sort((a,b)=> b.area -a.area)
                    return{
                         ...state,
                          countries:Area
                            };
        case 'POST_ACTIVITY':
                    return{
                        ...state,
                        
                        };

        case 'FILTER_BY_POPULATION':
             const pobl= action.payload === 'desc' ? state.countries.sort((a,b)=> a.population - b.population):
             state.countries.sort((a,b)=> b.population - a.population)
                return{
                      ...state,
                     countries:pobl
                    };
        case 'FILTER_BY_Az':
            const nomb= action.payload === 'asc' ? state.countries.sort(function(a,b){
                if(a.name > b.name){
                    return 1;
                };
                if (b.name > a.name){
                    return -1;
                };
                return 0;
            }) : state.countries.sort(function(a,b){
                if(a.name > b.name){
                    return -1;
                };
                if (b.name > a.name){
                    return 1;
                };
                return 0;
            });
                return{
                     ...state,
                     countries:nomb
                        };
        case 'FILTER_BY_ACTIVITY':
               const aux=[]
               state.allCountries.map(el=> el.Tourisms.forEach(element => {
                   if (element.name === action.paiload){
                       aux.push(el)
                   };
                   
               }));
                 return{
                     ...state,
                     countries:aux
                     };
        case 'GET_DETAIL':
                return{
                     ...state,
                    detail:action.payload
                    };
        default : {
                return state;
              }
       
    }
};

export default rootReducer;