import axios from 'axios';


export function getCountries(){
    return async function(dispatch){
        var json = await axios ('http://localhost:3001/countries');
        return dispatch({
            type: 'GET_COUNTRIES',
            payload: json.data
        })
    }
    };

export function getSearch(name){
    return async function (dispatch){
        try{
            var json =await axios.get("http://localhost:3001/countries?name="+name)
            return dispatch({
                type: "GET_SEARCH",
                payload:json.data
            })
        }
        catch(error){
            console.log(error);
        }
    }} ;
    
export function getActivity(){
    return async function(dispatch){
        var json = await axios.get("http://localhost:3001/tourisms");
        return dispatch({
            type:'GET_ACTIVITY',
            payload: json.data
        })
    }};

export function getDetail(id){
    return async function(dispatch){
        try{
          var json= await axios.get("http://localhost:3001/countries/"+id)
          return dispatch({
              type:'GET_DETAIL',
              payload: json.data
          })

        }
        catch(error){
            console.log(error);
        }
}};

export function postActivity(payload){
    return async function(dispatch){
        var json = await axios.get("http://localhost:3001/tourisms", payload);
        return dispatch({
            type:'POST_ACTIVITY',
            payload: json.data
        })
    }};

