import './Home.css';
import React, { useState } from 'react';
import  { useEffect} from 'react';
import {useDispatch,useSelector} from 'react-redux';
import {getCountries,filterCountry,filterPopulation,filterAz,getActivity,filterActivity,filterArea} from '../actions';
import {Link} from 'react';
import Card from './Card';
import Pagind from './Paginng';
import SearchBar from './Searchbar';
import StyleHome from './home.modul.css';


export default function Home (){

    const dispatch = useDispatch ();
    const allCountries = useSelector((state)=> state.countries);
    const [currentPage,setcurrentPage]= useState(1);
    const [countriesxPpage]= useState (9);
    const [,setOrder]=useState('');
    const [,setAZ]=useState('');
    const lastCountry= currentPage * countriesxPpage ;
    const firstCountry= lastCountry - countriesxPpage;
    const countryPresentPage= allCountries.slice(firstCountry,lastCountry);
    const  nameActivity= useSelector((state)=>state.actividad);
    const  paginated=(NunberPage) => (setcurrentPage(NunberPage));
    const  probe=nameActivity.map((el)=>el.name);
    const  only=[...new Set(probe)]; 

    useEffect (()=>{ 
        dispatch(getCountries());  //traigo los paises  
    },[dispatch]);

    useEffect (()=>{ 
        dispatch(getActivity());  //traigo las actividades 
    },[dispatch]);

    function handleClick(e){   //resetea paises
        e.preventDefault();
        dispatch(getCountries)
    };
    
    function handleFilterSate(event){

        dispatch(filterCountry(event.target.value));
    };

    function handleFilterPopulation(event){
        event.preventDefault();
        dispatch(filterPopulation(event.target.value));
        setcurrentPage(1);
        setOrder(`orden ${event.target.value}`);

    };

    function handleFilterArea(event){
        event.preventDefault();
        dispatch(filterArea(event.target.value));
        setcurrentPage(1);
        setOrder(`orden ${event.target.value}`);
    };

    function handleFilterAZ(event){
        event.preventDefault();
        dispatch(filterAz(event.target.value));
        setcurrentPage(1);
        setAZ(`orden ${event.target.value}`);

    };

    function handleActivity(event){
        dispatch(filterActivity(event.target.value));
    }
    return (
        
        <div className='container'>

            <div  className='searchbar'>
                <SearchBar/>
                   <div className='ToLanding'>
                    <Link to='/'><button className={StyleHome.btnAdmin}>Landing</button></Link>
                   </div>
                <div className='ToActivities'>
                    <Link to='/activity'><button className={StyleHome.btnAdmin}>Create Activities</button> </Link>
                </div>
            </div>

            
            <div  className='Botones Principales'>
                {only.length === 0?
                     <p className='CrearActividad'>Crear actividades para filtrar</p>
                     :<select  className={StyleHome.btnAdmin} onChange={event => handleActivity(event)}>
                      {only.map((event)=>(
                        <option value={event}>{event}</option>
                      ))
                      }  
                     </select>
                }
                <select className={StyleHome.btnAdmin} onChange={event => handleFilterSate(event)}>
                
                      <option value={'All'}>Todos</option>
                      <option value={'Africa'}>Africa</option>
                      <option value={'Americas'}> America</option>
                      <option value={'Antartic'}>Antartida</option>
                      <option value={'Asia'}> Asia</option>
                      <option value={'Europe'}>Europa</option>
                      <option value={'Oceania'}>Oceania</option>
                </select>

                <select className={StyleHome.btnAdmin} onChange={event => handleFilterArea(event)}>
                      <option value={'asc'}>Mayor Superf</option>
                      <option value={'desc'}>Menor Superf</option>

                </select>

                <select className={StyleHome.btnAdmin} onChange={event => handleFilterPopulation(event)}>
                      <option value={'asc'}>Mayor Poblacion</option>
                      <option value={'desc'}>Menor Poblacion</option>

                </select>

                <select className={StyleHome.btnAdmin} onChange={event => handleFilterAZ(event)}>
                      <option value={'asc'}> a-z</option>
                      <option value={'desc'}>z-a</option>

                </select>

            <button className={StyleHome.btnAdmin} onChange ={event => handleClick(event)}>Recargar Paises</button>
          </div>
        <br></br>
        <div className='paginando'>
             <Pagind>
              countrixpaige={countriesxPpage}
              allcountris={allCountries.length}
              paginado={paginated}
               


             </Pagind>
        </div>
        {countryPresentPage.map(el => {  
          return(
          <div key={el.id} className='card'>
          <Link to={"/home" + el.id}>
           <Card name={el.name}  continente={el.continente}  imagen={el.imagen} poblacion={el.poblacion} />
          </Link>
          </div>

          )})};

    </div>

        

)};