import React from 'react';
import Styleadmin from './home.modul.css';

export default function Pagind ({countriesxPpage,allCountries,paginated}){

      const NunberPage=[];

      for(let i=1 ; i <= Math.ceil(allCountries/countriesxPpage);i++){  //n° pagina x hoja 
            NunberPage.push(i);
      };
      return (
           <nav>
                <ul className='paginado'>
                   {
                    NunberPage && NunberPage.map(N => (

                    <button key={N} className={Styleadmin.btnAdmin} onClick={()=>paginated(N)}>{N}</button>
                    ))
                   };
                </ul>
           </nav>

      );



};